import { Pipe, PipeTransform, Renderer2 } from '@angular/core';
import { FnI18nService } from './fn-i18.service';

@Pipe({
    name: 'fnTranslate'
})
export class FnTranslatePipe implements PipeTransform {

    constructor(private i18n: FnI18nService, private render: Renderer2) {}

    transform(key: string) {
        const _span = this.render.createElement('span');
        this.render.setProperty(_span, 'innerHTML', this.i18n.translate(key));
        return _span.textContent;
    }
}
