import { Injectable } from '@angular/core';
import { Observable, Subject, throwError } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { FnMenuItemState } from '../models/fn-menu.state';
import { tap, catchError, map } from 'rxjs/operators';

@Injectable({
    providedIn: 'root'
})
export class FnMenuStoreService {
    constructor(private http: HttpClient) { }
    private readonly MENU_ID_KEY = 'id';
    private readonly MENU_ARRAY_KEY = 'menuItemArray';
    private readonly MENU_INFO_KEY = 'detail';
    private readonly MENU_PARENT_ARRAY_KEY = 'parentArray';

    getMenuMapOnId(rootMap: object, arrayObject: any[]) {
        if (arrayObject) {
            for (let i = 0; i < arrayObject.length; i++) {
                if (arrayObject[i][this.MENU_ID_KEY] !== 'undefined') {
                    rootMap[arrayObject[i][this.MENU_ID_KEY]] = arrayObject[i];
                    this.getMenuMapOnId(rootMap, arrayObject[i][this.MENU_ARRAY_KEY]);
                }
            }
        }
    }

    simpleMenuMapOnId(rootMap: object, arrayObject: any[], parentKey?: string) {
        if (arrayObject) {
            for (let i = 0; i < arrayObject.length; i++) {
                if (arrayObject[i][this.MENU_ID_KEY] !== undefined) {
                    const menuArray = [];
                    if (parentKey !== undefined && rootMap[parentKey][this.MENU_PARENT_ARRAY_KEY] !== undefined) {
                        rootMap[parentKey][this.MENU_PARENT_ARRAY_KEY].forEach((value: any) => {
                            menuArray.push(value);
                        });
                    }

                    const tempObj = {
                        id: arrayObject[i][this.MENU_ID_KEY], name: arrayObject[i].name, url: arrayObject[i].url,
                        len: arrayObject[i][this.MENU_ARRAY_KEY].length, showBreadcrum: arrayObject[i].showBreadcrum
                    };
                    menuArray.push(tempObj);

                    rootMap[arrayObject[i][this.MENU_ID_KEY]] = {};
                    //  rootMap[arrayObject[i][this.MENU_ID_KEY]] = menuArray;
                    rootMap[arrayObject[i][this.MENU_ID_KEY]][this.MENU_INFO_KEY] = tempObj;
                    rootMap[arrayObject[i][this.MENU_ID_KEY]][this.MENU_PARENT_ARRAY_KEY] = menuArray;

                    this.simpleMenuMapOnId(rootMap, arrayObject[i][this.MENU_ARRAY_KEY], arrayObject[i][this.MENU_ID_KEY]);
                }
            }
        }
    }

    getMenuData(menuId: string) {
        return this.http.get<FnMenuItemState>('https://raw.githubusercontent.com/VasimHayat/mock_data/master/menu.json')
            .pipe(
                // tslint:disable-next-line: no-string-literal
                map((menuData: any) => menuData.menuDataHash.menuData),
                catchError(error => throwError(error))
            );
    }


    // getMenuData(menuId: string) {
    //     const returnObs = new Subject();
    //     this.http.get('dummy.menu.json').subscribe(
    //         (resp: FnMenuItemState) => {
    //             returnObs.next(resp);
    //             returnObs.complete();
    //         },
    //         error => {
    //             returnObs.error(error);
    //             returnObs.complete();
    //         }
    //     );


    // }
}
