import { Component, OnDestroy, AfterContentInit, Renderer2, OnInit, Output, EventEmitter, ViewEncapsulation } from '@angular/core';
import { FnMenuItemState } from '../fn-stores/models/fn-menu.state';
import { Store, select } from '@ngrx/store';
import { FnMenuService } from '../fn-menu.service';
import { Subscription, Subject } from 'rxjs';
import { getMenuItemMap, getMenuData } from '../fn-stores/reducer/fn-menu.reducer';
import { takeUntil } from 'rxjs/operators';
import { SetActiveMenuId } from '../fn-stores/actions/fn-menu.action';
import { Router } from '@angular/router';


@Component({
    selector: 'fn-menu-sidebar',
    templateUrl: 'fn-menu.sidebar.component.html',
    styleUrls: ['fn-menu.sidebar.component.scss'],
    encapsulation: ViewEncapsulation.None

})
export class FnMenuSidebarComponent implements OnDestroy, AfterContentInit, OnInit {

    @Output() readonly fnClick = new EventEmitter<string>();

    private subscription = new Subscription();
    private destroy$: Subject<boolean> = new Subject<boolean>();
    sidebarHeight: string;
    public isMenuLoded: boolean;
    private menuItemMap: object;
    public menuData: FnMenuItemState;
    private SUB_MENU_LANDING_URL = 'zip/main';


    constructor(private fnMenuStore: Store<FnMenuItemState>,
        private fnMenuService: FnMenuService,
        private renderer: Renderer2,
        private router: Router) {
        this.subscribeMenuData();
    }


    private subscribeMenuData() {
        this.fnMenuStore.pipe(
            takeUntil(this.destroy$),
            select(getMenuData)
        ).subscribe(
            menuData => {
                this.menuData = menuData;
                this.isMenuLoded = true;
                this.subscribeMenuItemMap();
            }
        );
    }
    private subscribeMenuItemMap() {
        this.fnMenuStore.pipe(
            takeUntil(this.destroy$),
            select(getMenuItemMap)
        ).subscribe(
            _menuItemMap => this.menuItemMap = _menuItemMap
        );
    }

    private navigateToUrl(_url: string) {
        if (_url == this.SUB_MENU_LANDING_URL) {
            _url += '/submenu';
        }
        this.router.navigateByUrl(_url);
    }

    private menuClickSubscriber() {
        this.fnMenuService.menuItemClick$.pipe(
            takeUntil(this.destroy$)
        ).subscribe(
            menuId => {
                this.fnMenuStore.dispatch(new SetActiveMenuId(menuId));
                this.fnClick.emit(menuId);
                // this.menuItemMap.get(menuId)
                // todo routing help
                console.log(this.menuItemMap[menuId]);
                this.navigateToUrl(this.menuItemMap[menuId].detail.url);
            }
        );
    }

    ngOnInit(): void {
        this.menuClickSubscriber();
        this.calSidebarHeight();
    }

    ngAfterContentInit() {
        this.listenWindowResize();
    }

    calSidebarHeight() {
        //this.sidebarHeight = (window.innerHeight -56) + 'px';
    }

    listenWindowResize() {
        this.subscription.add(this.renderer.listen(window, 'resize', () => {
            // TODO resizeing
            this.calSidebarHeight();
        }));
    }

    ngOnDestroy(): void {
        this.subscription.unsubscribe();
        this.destroy$.next(true);
        this.destroy$.unsubscribe();
    }



}
