import { Component, Input, ViewEncapsulation } from '@angular/core';
import { FnMenuItemState } from '../fn-stores/models/fn-menu.state';
import { FnMenuService } from '../fn-menu.service';

@Component({
    selector: '[fn-menu-item]',
    encapsulation: ViewEncapsulation.None,
    templateUrl: 'fn-menu.item.component.html',
    styles: [
        `
li{list-style:none}
.nav-item .nav-link:hover {
    color: rgba(255, 255, 255, 0.75);
    background-color: #596773;
}
  .nav-item .nav-link:hover img{
    opacity: 1;
}
  .nav-item .nav-link{
    border-left: 2px solid transparent;
    font-size: 13px;
    letter-spacing: 0.05em;
    padding: .7rem 0.7rem;
}
  .nav-item.active> .nav-link{
    border-left: 2px solid #35c4ff;
    background-color: #465766;
}
  .nav-item .nav-link img{
    height: 21px;
    width: 21px;
    margin-right: 15px;
    margin-top: -3px;
    opacity: 0.7;
}
 > .nav-item.active> .nav-link img{
    opacity: 1;
}
 > .nav-item ul.nav-second-level > .nav-item {
    padding-left: 0px;
}
 > .nav-item ul.nav-second-level > .nav-item > .nav-link {
    padding-left: 20px;
}
 > .nav-item ul.nav-second-level {
    padding-left: 0;
}
.sidebar-menu-item{display:none}
.nav-item  {
    width: 300px;
}
`
    ]
})
export class FnMenuItemComponent {


    public renderMenuItem: FnMenuItemState;
    @Input() fnMenuItem: FnMenuItemState;

    constructor(private fnMenuService: FnMenuService) { }
    menuClickHandler(menuItem: FnMenuItemState) {
        this.fnMenuService.onMenuItemClick(menuItem.id);
    }
}
