import { Component, forwardRef, Input, OnInit, Output, EventEmitter, ViewEncapsulation } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { FnInputValueAccessor } from '../../../core/model/fn-input.value.accessor';
import { FN_INPUT_TYPES } from '../../../core/index';

@Component({
  selector: 'fn-base-input',
  templateUrl: 'fn-base-input.component.html',
  styleUrls: ['fn-base-input.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => FnBaseInputComponent),
      multi: true
    }
  ]
})
export class FnBaseInputComponent extends FnInputValueAccessor<any> implements OnInit {

  // @ts-ignore
  inputTypeMap = FN_INPUT_TYPES;

  @Input() name: string;
  @Input() id: string;
  @Input() placeholder: string;
  @Input() isDisabled: boolean;
  @Input() textAlign: 'left' | 'right' | 'center' | 'justify' = 'left';
  @Input() isErrorBox = true;
  @Input() minLength = 0;
  @Input() isRequired = false;
  @Input() maxLength = 255;
  @Input() type: string;
  @Input() parentForm: any;
  @Input() readonly: boolean = null;
  @Input() autocomplete = 'off';
  @Input() tabindex = 0;
  @Input() pattern?: any;

  @Output() focusEvent: EventEmitter<any> = new EventEmitter();
  @Output() blueEvent: EventEmitter<any> = new EventEmitter();

  ngOnInit(): void {
    this.setOption();
  }

  setOption() {
    this.placeholder = this.placeholder || this.name;
  }


}
