import { Component, Input, Output, EventEmitter, forwardRef, ViewEncapsulation, OnInit } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { FnInputValueAccessor } from '../../../core';

@Component({
    selector: 'fn-multi-select',
    templateUrl: 'fn-multi-select.component.html',
    styleUrls: ['fn-multi-select.component.scss'],
    encapsulation: ViewEncapsulation.None,
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => FnMultiSelectComponent),
            multi: true
        }
    ]
})
export class FnMultiSelectComponent extends FnInputValueAccessor<string> implements OnInit {

    constructor() { super(); }

    @Input() name: string;
    @Input() id: string;
    @Input() isDisabled: boolean;
    @Input() isErrorBox = true;
    @Input() optionList: Array<any> = [];
    @Input() labelKey: string;
    @Input() placeholder: string;
    @Input() isRequired = false;
    @Input() parentForm: any;
    @Input() tabIndex = 0;
    @Input() groupBy: string;
    @Input() maxSelectedItems = 0;

    @Output() changeEvent: EventEmitter<any> = new EventEmitter();
    @Output() closeEvent: EventEmitter<any> = new EventEmitter();

    ngOnInit(): void {
        this.setOption();
    }

    setOption() {
        this.placeholder = this.placeholder || this.name;
    }
}
