import { DEFAULT_MENU_STATE, FnMenuItemState, FnMenuState } from '../models/fn-menu.state';
import { FnMenuActionType, FnMenuActions } from '../actions/fn-menu.action';
import { createFeatureSelector, createSelector } from '@ngrx/store';

const initMenuState: FnMenuState = {
    menuData: DEFAULT_MENU_STATE,
    error: '',
    activeMenuId: '',
    menuItemMap: null,
    menuItemDetailMap: null,
    isMenuLoaded: false
};


const fnMenuFeatureState = createFeatureSelector<FnMenuState>('FnMenuReducer');


export const getMenuData = createSelector(
    fnMenuFeatureState,
    state => state.menuData // may be we filter some item
);
export const getRooMenuId = createSelector(
    fnMenuFeatureState,
    state => state.menuData.id // may be we filter some item
);
export const getActiveMenuId = createSelector(
    fnMenuFeatureState,
    state => state.activeMenuId
);
export const getMenuItemMap = createSelector(
    fnMenuFeatureState,
    state => state.menuItemMap
);
export const getMenuDetailItemMap = createSelector(
    fnMenuFeatureState,
    state => state.menuItemDetailMap
);
export const getMenuItemById = createSelector(
    getMenuDetailItemMap,
    (state, prop) => {
        return state[prop.id];
    }
);

export const getActiveMenuItem = createSelector(
    getMenuDetailItemMap,
    getActiveMenuId,
    (state, activeMenuId) => {
        return state[activeMenuId];
    }
);

export const getMenuStatus = createSelector(
    fnMenuFeatureState,
    state => {
        return state.isMenuLoaded;
    }
);

export function FnMenuReducer(state = initMenuState, action: FnMenuActions): FnMenuState {

    switch (action.type) {
        case FnMenuActionType.InitMenuState:
            return {
                ...state
            };
        case FnMenuActionType.LoadMenuDataSucess:
            return {
                ...state,
                menuData: action.menuItemState,
                menuItemMap: action.menuItemMap,
                menuItemDetailMap: action.menuItemDetailMap,
                isMenuLoaded: action.isMenuLoaded
            };
        case FnMenuActionType.LoadMenuDataError:
            return {
                ...state,
                error: action.error
            };
        case FnMenuActionType.SetActiveMenuId:
            return {
                ...state,
                activeMenuId: action.activeMenuId
            };
        default:
            return state;
    }
}
